import { Breadcrumb, Spin, Avatar, Typography, Row, Col, Statistic, Radio } from 'antd';
import { FlagOutlined, FrownOutlined, MehOutlined, MinusOutlined, PlusOutlined, ProjectOutlined, SmileOutlined, TrophyOutlined } from '@ant-design/icons';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import api from '../api';
import CareerTable from '../components/CareerTable';
import DuelInfo from '../duel/DuelInfo';

const ManagerDetail = (props) => {

    const [manager, setManager] = useState();    
    const [level, setLevel] = useState(0);
    const [leagues, setLeagues] = useState();    

    useEffect(() => {
        const id = props.match.params.itemID;
        axios({
            method: 'GET',
            url: `${api.managers}/${id}`
        }).then(res => {            
            console.log(manager)             
            setManager(res.data)                 
        }).catch(err => {
            console.log(err.message)
        });    
        axios({
            method: 'GET',
            url: `${api.leagues}`
        }).then(res => {                   
            setLeagues(res.data)                 
        }).catch(err => {
            console.log(err.message)
        });    
    }, [props.match])        

    function getChampion(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_champion
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_champion
            }
        }
        return res
    }

    function getRunnerup(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_runnerup
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_runnerup
            }
        }
        return res
    }

    function getThird(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_third
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_third
            }
        }
        return res
    }

    function getTopScorer(manager, level) {        
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_topscorer
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_topscorer
            }
        }
        return res
    }

    function getVanga(manager, level) {        
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_vanga
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_vanga
            }
        }
        return res
    }

    function getPoints(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_point
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_point
            }
        }
        return res
    }

    function getWins(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_win
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_win
            }
        }
        return res
    }

    function getDraws(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_draw
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_draw
            }
        }
        return res
    }

    function getLosses(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_loss
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_loss
            }
        }
        return res
    }

    function getScore(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_score
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_score
            }
        }
        return res
    }

    function getScoreAway(manager, level) {
        let res = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_score_away
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_score_away
            }
        }
        return res
    }

    function getAverageScore(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_score
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_score
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / count
        return res.toFixed(1)
    }

    function getAverageScoreAway(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_score_away
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_score_away
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / count
        return res.toFixed(1)
    }

    function getAveragePoints(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_point
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_point
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / count
        return res.toFixed(1)
    }

    function getAverageTopScorer(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_topscorer
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_topscorer
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / count
        return res.toFixed(1)
    }

    function getAverageWins(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_win
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_win
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / count
        return res.toFixed(1)
    }

    function getAverageDraws(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_draw
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_draw
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / count
        return res.toFixed(1)
    }

    function getAverageLosses(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_loss
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_loss
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / count
        return res.toFixed(1)
    }

    function getAverageMatchScore(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_score
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_score
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / (count * 9)
        return res.toFixed(1)
    }

    function getAverageMatchScoreAway() {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_score_away
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_score_away
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / (count * 9)
        return res.toFixed(1)
    }

    function getAverageMatchPoints(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_point
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_point
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / (count * 9)
        return res.toFixed(1)
    }

    function getMatchTopScorerRate(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_topscorer
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_topscorer
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / (count * 9) * 100
        return res.toFixed(1)
    }

    function getMatchWinRate(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_win
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_win
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / (count * 9) * 100
        return res.toFixed(1)
    }

    function getMatchDrawRate(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_draw
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_draw
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / (count * 9) * 100
        return res.toFixed(1)
    }

    function getMatchLossRate(manager, level) {
        let res = 0
        let count = 0
        if (level === 0) {
            manager.career.forEach(career => {
                res += career.total_loss
                count += career.total_appearance
            })
        } else {
            let career = manager.career.find(x => x.level === level)
            if (career) {
                res = career.total_loss
                count = career.total_appearance
            }
        }
        if (count === 0) {
            return 0
        }
        res = res / (count * 9) * 100
        return res.toFixed(1)
    }

    function getTeamScoreAway(league, manager) {
        let res = 0
        league.gameweeks.forEach(week => {
            week.matches.forEach(match => {
                if (match.home_team.id === manager.id) {
                    res += match.away_score
                } else if (match.away_team.id === manager.id) {
                    res += match.home_score
                }
            })
        })
        return res
    }

    function getTeams() {
        let teams = []
        let list = level === 0 ? leagues : leagues.filter(x => x.level === level)
        list.forEach(league => {
            let team = league.table.teams.find(x => x.manager.id === manager.id)      
            if (team) { 
                let data = {
                    id: team.id,
                    name: team.name,
                    rank: team.rank,
                    points: team.points,
                    score: team.score,
                    score_away: getTeamScoreAway(league, team.manager),
                    wins: team.wins,
                    draws: team.draws,
                    losses: team.losses,
                    year: league.year,
                    number: league.number
                }     
                if (league.level === 1) {
                    data.level = ""
                } else if (league.level === 2) {
                    data.level = ""
                }
                teams.push(data)
            }            
        });
        return teams;
    }

    function onChangeLevel(e) {        
        setLevel(e.target.value)
    }

    return (
        <div>
            { manager && leagues ? (            
            <div>
                <Breadcrumb>
                    <Breadcrumb.Item>
                        <a href="/"></a>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item>
                        <a href="/managers"></a>
                    </Breadcrumb.Item>
                    <Breadcrumb.Item>
                        {manager.name}
                    </Breadcrumb.Item>
                </Breadcrumb>
                <div style={{ margin: '16px 0', padding: '16px', backgroundColor: '#fff' }}>    
                    <Row gutter={[16, 16]}>
                        <Col xs={24} sm={12} style={{ textAlign: 'end' }}>                            
                            <Avatar shape="square" size={180} src={<img src={manager.image} alt="teamlogo" style={{ height: '100%', width: 'auto' }} />} />                    
                        </Col>
                        <Col xs={24} sm={12}>
                            {/* <Typography.Title level={3} style={{ margin: 0 }}></Typography.Title>      */}
                            <Typography.Title level={2} style={{ margin: 0 }}>{manager.name}</Typography.Title>                           
                            <Typography.Title level={5} style={{ marginBottom: 0 }}> - {getChampion(manager, level)}</Typography.Title>
                            <Typography.Title level={5} style={{ margin: 0 }}> - {getRunnerup(manager, level)}</Typography.Title>          
                            <Typography.Title level={5} style={{ margin: 0 }}>3-  - {getThird(manager, level)}</Typography.Title>                     
                            <Typography.Title level={5} style={{ margin: 0 }}>  - {getTopScorer(manager, level)}</Typography.Title>     
                            <Typography.Title level={5} style={{ margin: 0 }}> - {getVanga(manager, level)}</Typography.Title>                                
                        </Col>                    
                    </Row>           
                    <div style={{ margin: '16px' }}>
                        <Radio.Group onChange={onChangeLevel} defaultValue={level}>
                            <Radio.Button value={1}></Radio.Button>
                            <Radio.Button value={2}></Radio.Button>
                            <Radio.Button value={0}></Radio.Button>
                        </Radio.Group>
                    </div>
                    <Row gutter={16} style={{ margin: '8px' }}>
                        <Col xs={24} sm={24} md={12}>
                            <Row>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <TrophyOutlined style={{ fontSize: '24px' }} />   
                                </Col>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <PlusOutlined style={{ fontSize: '24px' }} />    
                                </Col>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <MinusOutlined style={{ fontSize: '24px' }} />    
                                </Col>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <ProjectOutlined style={{ fontSize: '24px' }} />    
                                </Col>                                                
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title="" value={getChampion(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title=" +" value={getScore(manager, level)} />
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title=" -" value={getScoreAway(manager, level)} />
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title=" " value={getPoints(manager, level)} />  
                                </Col>                                   
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getRunnerup(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title="+ (1 )" value={getAverageScore(manager, level)} />
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title="- (1 )" value={getAverageScoreAway(manager, level)} />
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title=" (1 )" value={getAveragePoints(manager, level)} />  
                                </Col>                        
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getThird(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title="+ (1 )" value={getAverageMatchScore(manager, level)} />
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title="- (1 )" value={getAverageMatchScoreAway(manager, level)} />
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>                                                
                                    <Statistic title=" (1 )" value={getAverageMatchPoints(manager, level)} />  
                                </Col>
                            </Row>
                        </Col>
                        <Col xs={24} sm={24} md={12}>
                            <Row>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <FlagOutlined style={{ fontSize: '24px' }} />    
                                </Col>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <SmileOutlined style={{ fontSize: '24px' }} />    
                                </Col>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <MehOutlined style={{ fontSize: '24px' }} />    
                                </Col>
                                <Col span={6} style={{ border: '1px solid #f1f1f1', display: 'flex', alignItems: 'center', justifyContent: 'center', padding: '16px' }}>
                                    <FrownOutlined style={{ fontSize: '24px' }} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getTopScorer(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getWins(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getDraws(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getLosses(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getAverageTopScorer(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getAverageWins(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getAverageDraws(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={getAverageLosses(manager, level)} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={`${getMatchTopScorerRate(manager, level)}%`} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={`${getMatchWinRate(manager, level)}%`} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={`${getMatchDrawRate(manager, level)}%`} />    
                                </Col>
                                <Col span={6} style={{ textAlign: 'center', border: '1px solid #f1f1f1' }}>
                                    <Statistic title=" " value={`${getMatchLossRate(manager, level)}%`} />    
                                </Col>
                            </Row>
                        </Col>                                                  
                    </Row>                   
                    <div style={{ margin: '16px' }}>
                        <Typography.Title level={4}></Typography.Title>
                        <CareerTable data={getTeams()} />
                    </div>
                    <div style={{ margin: '16px' }}>
                        <Typography.Title level={4}></Typography.Title>
                        <DuelInfo manager={manager} />
                    </div>
                </div>
            </div>
            ) : (
                <div style={{ width: '100%', height: '60vh', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                    <Spin />
                </div>
            )}
        </div>
    );
};

export default ManagerDetail;